//     {clothes_id: 49, fabric: '- 95% поліестер, 5% шерсть.', details_and_cut: '- крій зі спущеними плечима;\n- застібається на кнопки;\n- дві прорізні кишені з боків і дві накладні на грудях;\n- манжети та нагрудні кишені на кнопках;\n- бірка з фірмовим логотипом Staff вшита у бічний шов.', color: 'мікс', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '- зріст моделі - 175 см;\n- розмір речей - "S".',  describe: 'Вільна куртка-сорочка – це щось середнє між верхнім одягом і сорочкою. Модель виготовлена із цупкого матеріалу з додаванням шерсті, тому вона ідеально підійде для створення стильного та комфортного образу.'},
//     {clothes_id: 50, fabric: ' 100% поліестер;\n- всередині високоякісний утеплювач Hollowsoft.', details_and_cut: '- подовжений тип крою зі спущеними плечима;\n- високий комір;\n- екохутро кріпиться на кнопках і блискавці, за необхідності швидко знімається;\n- якісний тракторний двоходовий замок-блискавка доповнений вітрозахисною планкою на липучках;\n- у рукавах є додаткові трикотажні манжети;\n- п\'ять зовнішніх кишень: одна нагрудна на блискавці, дві прикриті планкою на липучках і дві відкриті прорізні;\n- одна внутрішня вертикальна кишеня на блискавці зліва;\n- з обох боків розрізи на кнопках (довжина розрізу 32 см);\n- бірка з фірмовим логотипом бренду на кишені.', color: 'чорний', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '- зріст моделі - 172 см;\n- розмір речей - "S".',  describe: 'Довга жіноча зимова куртка із хутром, що легко знімається, на капюшоні та розрізами на кнопках з боків для зручності у використанні. Прорізні кишені забезпечать комфортне укриття для рук, а подвійний бігунок на блискавці дозволить регулювати мікроклімат. Це чудовий варіант для холодної погоди, який вдало доповнить і зробить теплішим будь-який лук.\n'},
