//     {clothes_id: 66, fabric: '- 80% поліестер, 20% бавовна.', details_and_cut: 'Верх:\n- укорочений крій;\n- відкладний комір;\n- застібається на ґудзик.\n\nНиз:\n- висока посадка;\n- зліва застібка на блискавці;\n- спереду внизу розріз (довжина розрізу 10 см).', color: 'чорний/білий', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Жіночий костюм допоможе створити яскравий та елегантний образ для роботи чи прогулянки. Із ним ти завжди будеш в тренді.'},
//     {clothes_id: 67, fabric: '- натуральна шкіра з глянцевим покриттям;\n- всередині екохутро;\n- підошва з поліуретану.', details_and_cut: '- підошва прошита по всьому периметру виробу;\n- висота підошви: спереду – 3,5 см, ззаду - 5 см;\n- класична система шнурівки з двома гачками зверху;\n- логотип бренду на язичку.', color: 'чорний', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Комфортні жіночі черевики на високій платформі доповнять будь-який лук. Конструкція виробу стабілізує і фіксує стопу, завдяки чому взуття є дуже зручним. А якісне екохутро всередині гарантує приємне тепло навіть у найсильніші морози.'},
//     {clothes_id: 68, fabric: '- еко-замша;\n- гумова підошва.', details_and_cut: '- висота підошви: спереду - 3,5 см, ззаду - 5 см;\n- підсилювач носка та п\'яти;\n- м\'який язичок;\n- класична система шнурівки;\n- петля ззаду зверху.', color: 'темно-рожевий', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Демісезонна модель виконана з якісної еко-замші, що дозволяє виробу не змінювати зовнішній вигляд та зберігати оригінальне забарвлення при тривалому використанні. Завдяки зручній платформі кеди стануть ошатним доповненням до твого повсякденного одягу.'},
//     {clothes_id: 69, fabric: '- натуральна замша;\n- поліуретанова підошва, всередині якої — матеріал Eva, що характеризується чудовою амортизацією, зносостійкістю та невеликою вагою.', details_and_cut: '- висота підошви: спереду – 2,5 см, ззаду – 3,5 см;\n- петля на язичку для комфортної фіксації;\n- класична система шнурівки.', color: 'чорний', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Зручні та легкі кросівки чудово поєднуються з будь-яким стилем. Акуратна форма надає виразності та дозволяє зручно зафіксувати ногу для тривалого носіння.'},
//     {clothes_id: 70, fabric: '- 100% поліестер.', details_and_cut: '- розмір: висота 28 см, ширина 23 см, ширина збоку 9 см;\n- низ рюкзака посилено додатковим шаром тканини;\n- якісна фурнітура;\n- два відділення на блискавці та дві накладні кишені на резинці з боків;\n- усередині основного відділення прорізна кишеня на блискавці;\n- плечові ремені регулюються за довжиною;\n- бірка з фірмовим логотипом бренду вшита у бічний шов.', color: 'темно-сірий', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Стильний та компактний рюкзак у мінімалістичному стилі, який легко замінить невелику сумку на щодень і чудово підійде для подорожей.'},
