//     {clothes_id: 72, fabric: '- міцний та водонепроникний 100% поліестер.', details_and_cut: '- розмір 32*15*8 см;\n- фурнітура YKK;\n- два відділення на блискавці;\n- накладна відкрита кишеня усередині основного відділення;\n- ремінна стрічка з пластиковим фастексом, довжина ремінної стрічки регулюється (максимальна довжина – 110 см);\n- бірка з фірмовим логотипом Staff вшита у бічний шов.', color: 'сірий камуфляж', supervision: '- прання у звичайному режимі при температурі не вище 30°С, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Зручна поясна сумка для твого комфорту, яка поєднує у собі яскравий дизайн і функціональність. Модель унісекс чудово комбінується з різними стилями одягу.'},
//     {clothes_id: 73, fabric: '- міцний та водонепроникний поліестер.', details_and_cut: '- розмір 28*14 см;\n- дві кишені на блискавці;\n- пластиковий карабін для ключів та кишеня на блискавці всередині великого відділення;\n- фурнітура YKK;\n- пасова стрічка з пластиковим фастексом, довжина пасової стрічки регулюється (максимальна довжина – 110 см);\n- задня частина сумки - обшита сіткою;\n- бірка з фірмовим логотипом Staff нашита на пасову стрічку.', color: 'світлий беж/принт', supervision: '- прання у звичайному режимі при температурі не вище 30°С, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Поясна сумка - це дуже зручний тренд-аксесуар, в який ти зможеш покласти все найнеобхідніше та цінне.'},
//     {clothes_id: 74, fabric: '- якісна екошкіра;\n- підкладка 100% поліестер.', details_and_cut: '- об\'єм: 15 л;\n- розмір: висота 34 см, довжина 41 см, ширина 14 см;\n- велике відділення на блискавці, зверху доповнене двома кнопками з боків для регулювання об\'єму;\n- зовнішня кишеня на блискавці;\n- усередині сумки прорізна кишеня на блискавці;\n- фурнітура YKK;\n- ручки пришиті (довжина ручок 33 см);\n- низ сумки посилений додатковим шаром тканини;\n- прогумована нашивка з логотипом Staff на лицьовій стороні.', color: 'чорний', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Містка сумка шоппер стане чудовим доповненням як для повсякденних, так і для ділових образів. Завдяки витонченому дизайну сумка чудово підійде також для роботи або навчання.'},
