//     {clothes_id: 77, fabric: '- 50% вовна, 50% акрил.', details_and_cut: '- висота 20 см;\n- ширина 20 см (в розтягнутому вигляді 25 см).', color: 'беж', supervision: '- прання у звичайному режимі при температурі не вище 30°C, без віджиму;\n- відбілювання заборонено;\n- сушити в підвішеному стані, без застосування штучної сушки.', on_the_photo: '-',  describe: 'Стильна шапка з рубчастого трикотажу та м\'яким помпоном підкреслить твій стиль і додасть комфорту в холодну пору року.'},
